@import "src/variables";

.instagram {

  &__row {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    font-size: 24px;
    color: #0AA566;
    font-weight: 800;
    padding: 5px 5px 5px 38px;

    line-height: 30px;

    cursor: pointer;

    transition-duration: 0.2s;

    &:hover {
      transform: scale(1.2, 1.2);
    }

    background: url("../../assets/images/instagram/instIcon.svg") left center no-repeat;

    margin-bottom: 45px;

    @media (max-width: 767px) {
      margin-bottom: 20px;
    }

  }

  &__gallery {
    display: grid;
    width: 100%;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  }

  &__item {
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}